$primary-color: #6f8f6f;
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	color: white;
	background: $primary-color;
	line-height: 1.4;
}
.container {
	max-width: 1100px;
	margin: 0 auto;
	padding: 0 2em;
	overflow: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	height: 100vh;
}
